/* eslint-disable */
import React, { FC, useState, useEffect } from 'react';
import yuanName from '../../assets/img/yuan1.png';
import person1 from '../../assets/img/person.png';
import time1 from '../../assets/img/time1.png';
import './index.less';
import { GetRequest, dateFormat } from '../../util/common';

export default (props) => {
    let requestParams = GetRequest(window.location.href);
    const [data, setData] = useState([])

    useEffect(() => {
        const { data, type, typeName } = props;
        const eventData = data.map(item => {
            return {
                ...item,
                title: item?.accidentName || '-',
                name: item?.receiver?.name  || '-',
                time: item?.happenTime ? dateFormat(new Date(item?.happenTime),'YYYY-mm-dd HH:MM:SS') : '-',
                alarmTime: item?.alarmTime ? dateFormat(new Date(item?.rectime),'YYYY-mm-dd HH:MM:SS') : '-',
                position: item?.position || '-',
                state: typeName,
                type: type === 'alarm' ? ' ' : item?.eventType?.value.slice(0,2),
                isAlarm: type
            }
        }) || []
        setData(eventData)
    }, [props])

    const eventDetails = (item) => {
        sessionStorage.setItem('eventDetails', JSON.stringify(item))
        window.location.href = `#/demo/eventDetails?tabIndex=${requestParams.tabIndex}`
    }

    return (
        <div className='eventData'>
            <div className='eventData-content'>
                {
                    data.length ? data.map(item => (
                        <div className='list' key={item.key} onClick={() => eventDetails(item)}>
                            <div className='title'>{item.title} <span className='type'>{item.type}</span></div>
                            <div className='item'><img src={time1}></img>&nbsp;事发时间<span>{item.time}</span><span className='state'>{item.state}</span></div>
                            <div className='item'><img src={yuanName}></img>&nbsp;接警人<span>{item.name}</span></div>
                            <div className='item'><img src={person1}></img>&nbsp;事发地点<span>{item.position}</span></div>
                        </div>
                    )) : <div style={{ margin: '30px 100px', textAlign: 'center' }}>暂无数据</div>
                }
            </div>
        </div>
    );
};
