/* eslint-disable */
import React, { FC, useEffect, useState } from 'react';
import { SupNavBar, SupNavBarProps } from '@sup-mobile/ui';
import { JSBridge } from '@sup-mobile/framework';
import { Tabs, WhiteSpace, Badge, List, Icon, Grid } from 'antd-mobile';
import './index.less';
import EventData from '../../../components/EventData'
import { getAlarmList, getEventList } from '../../../api/requestConfig'
import add from '../../../assets/img/add.png';
import { GetRequest } from '../../../util/common';
const EventEditing: FC<any> = (props: any) => {

  let requestParams = GetRequest(window.location.href);

  const [tabIndex, setTabIndex] = useState<any>(0);
  const [alarmList, setAlarmList] = useState<Array<any>>([]);
  const [eventList, setEventList] = useState<any>([]);
  const [unEventList, setUnEventList] = useState<Array<any>>([]);

  const tabs = [
    { title: <Badge>接警记录</Badge> },
    { title: <Badge>应急事件</Badge> },
    { title: <Badge>已关闭事件</Badge> },
  ];

  const addEvent = () => {
    window.location.href = window.location.hostname === 'localhost'
      ? `http://10.54.4.40:8080/msService/SESECD/alarmRecord/almAlarmRecord/alarmRecordEdit?clientType=mobile`
      : `${window.location.origin}/msService/SESECD/alarmRecord/almAlarmRecord/alarmRecordEdit?clientType=mobile`;
  }


  // 刷新
  const fillProps: SupNavBarProps = {
    mode: 'fill',
    rightContent: <div className='fillHeader'>
      <div className='fillRfresh' onClick={() => { addEvent() }}>
        <img src={add} style={{ width: '2rem' }} />
      </div>
    </div>,
    onLeftClick: () => {
      (window as any).iMobilejs.closeController();
    },

  };


  useEffect(() => {
    if (requestParams.tabIndex != undefined && requestParams.tabIndex != null && requestParams.tabIndex != '') {
      setTabIndex(Number(requestParams.tabIndex))
    }
    initData();
  }, [])

  const initData = async () => {
    const alarmList = await getAlarmList();
    setAlarmList(alarmList)
    const eventList = await getEventList(0);
    setEventList(eventList)
    const unEvnetLst = await getEventList(1);
    setUnEventList(unEvnetLst)
  }

  return (
    <div className="eventEditing-root">
      <SupNavBar  {...fillProps}>{'应急指挥'}</SupNavBar>
      <div style={{ height: 'calc( 100% - 45px )' }}>
        <Tabs
          tabs={tabs}
          page={tabIndex}
          onChange={(tab, index) => {
            setTabIndex(index);
            window.location.replace(`#/demo/eventList?tabIndex=${index}`)
          }}
        >
          <div className="tabs" data-value='1'>
            <EventData data={alarmList} type='alarm' typeName='未处理' />
          </div>

          <div className="tabs" data-value='2'>
            <EventData data={eventList} type='event' typeName='未关闭' />
          </div>
          <div className="tabs" data-value='3'>
            <EventData data={unEventList} type='unEvent' typeName='已关闭' />
          </div>
        </Tabs>
      </div>
    </div>
  );
};

export default EventEditing;
